.layout{height:100%;max-height:100%}.layout__header{padding:0;display:flex;justify-content:space-between;align-items:center;z-index:9;background-color:#fff !important;border-bottom:1px solid #ddd}.layout__header-main{padding:0 15px;flex:1;display:flex;justify-content:space-between;align-items:center}.layout__header-main .actions{height:100%;display:flex;justify-content:center;align-items:center}.layout__header-main .actions>*{margin-left:30px;height:100%;display:flex;align-items:center}.layout__header-main .actions .user-action{cursor:pointer}.layout__header .logo{cursor:pointer;height:64px;width:200px;box-sizing:border-box;display:flex;justify-content:center;align-items:center;z-index:9}.layout__header .logo img{width:auto;height:30px}.layout__sider{background-color:#fff !important;box-sizing:border-box;border-right:1px solid #f0f0f0;margin-bottom:10px}.layout__side-menu{border-right:none !important}.layout__side-menu .anticon{flex-shrink:0;width:1.5em;height:1.5em}.layout .ant-menu-inline-collapsed{width:79px !important}.layout__content{display:flex;flex-direction:column;flex:1;margin-left:10px;margin-bottom:10px;overflow-y:auto;background-color:#fff}.layout__content>:nth-child(1){min-height:100%;padding:16px}.layout__content>:nth-child(1) .page-header{justify-content:space-between;width:100%;margin-bottom:16px}
